
*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #999;
  font-size: 14px;
}
a{
  color: #999;
  text-decoration: none;
}
a:hover,.router-link-active{
  color:#ff6700;
}
img{ vertical-align: middle
}

/*通用*/

/*文字*/
.text-center{ text-align: center
}

/*颜色*/
.bg-light{ background-color:#f2f2f2;
}

/*栅格*/
.row{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    line-height: 43px;
}
.col{ -webkit-box-flex: 1; -ms-flex: 1; flex: 1;
}
.row-center{
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.padding{ padding: 10px;
}
.pl{
    padding-left: 10px;
}
.pr{
    padding-right: 10px;
}

/*组件*/

/*header*/
.header{
    height: 43px;
    width: 100%;
}
.header{ height: 43px;
}
.header input[type=text]{
    height: 29px;
    padding: 0 5px;
    border: none;
    margin-top: 7px;
}
.sub-header{ height: 43px;
}
.content{
    position: absolute;
    top:0; left:0; right:0; bottom:0;
}
.has-header.content,.has-sub-header.content{ top:43px
}
.has-header.has-sub-header.content{top:86px;
}
.has-footer.content{ bottom:49px;
}

/*tabs*/
.tabs{
    height: 49px;
    position:fixed;
    left:0;
    bottom:0;
    width: 100%;
}
.tabs a{ line-height: 49px;
}
.router-link-active{color:red;
}

/* 43 在ios 里面一个列表的高度   29一个列表图标的高度  15 间距   49tabs*/

h1[data-v-a020658a]{color:red;
}











.scroll .slider{
  padding-left: 15px;
  padding-right: 15px;
  float: left;
  line-height: 43px;
}


























































